import "../tree.scss";
import React from "react";
import { DownOutlined } from "@ant-design/icons";
import { Tree } from "antd";
const treeData = [
  {
    title: "",
    key: "0-0",
    children: [
      {
        title: "",
        key: "0-0-0",
      },
      {
        title: "",
        key: "0-0-1",
      },
      {
        title: "",
        key: "0-0-2",
      },
      {
        title: " ",
        key: "0-0-3",
      },
      {
        title: "   ",
        key: "0-0-4",
      },
      {
        title: ",,   ",
        key: "0-0-5",
      },
      {
        title: "   ",
        key: "0-0-6",
      },
      {
        title: "      ",
        key: "0-0-7",
      },
    ],
  },
];
const Realstate = () => {
  const onSelect = (selectedKeys, info) => {
    console.log("selected", selectedKeys, info);
  };
  return (
    <Tree
      className="tree"
      switcherIcon={<DownOutlined className="arrow_garden" />}
      defaultExpandedKeys={["0-0-0"]}
      onSelect={onSelect}
      treeData={treeData}
    />
  );
};
export default Realstate;
